<template>
  <div>
    <myBreadcrumb :breadList="breadList" />
    <div class="videomore lightest-bg">
      <div class="video-list lightest-bg">
        <div class="myVideo" v-for="(index, i) in videoList" :key="i">
          <img
            :src="index.images ? index.images.appdetailsmall : defaultPcBackImg"
            class="title"
          />
          <a href="javascript:void(0)" @click="goVideo(index)">
            <span
              class="iconfont icon-bofang lightest-text"
              id="play-picture"
            ></span>
          </a>
          <p class="text-1 dark-text">{{ index.title }}</p>
          <p class="text-2 light-text">{{ index.categoryTitle }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import myBreadcrumb from "../../../components/bread-crumb";
import { updatedBreadCrumb } from "@/assets/js/util.js";
export default {
  data() {
    return {};
  },
  created() {
    let title = sessionStorage.getItem("ClassicLabel");
    let data = {};
    data.path = this.$route.path;
    data.name = title;
    updatedBreadCrumb(data, 1, this);
  },
  methods: {
    goVideo(index) {
      var _this = this;
      this.$router.push({
        path: "/video/details"
      });
      this.$store.commit("setVideo", index);
    }
  },
  computed: {
    videoList() {
      return this.$store.state.videoList;
    },
    breadList() {
      return this.$store.state.breadList;
    }
  },
  components: {
    myBreadcrumb
  }
};
</script>
<style lang="scss">
.videomore {
  overflow: hidden;
  width: 1200px;
  margin: 22px auto 0;
  background: red;
}
</style>
